@font-face {
  font-family: "Titillium Web",sans-serif;
  src: local('Titillium Web'), url(/static/media/TitilliumWeb-Regular.57e937e4.ttf) format('ttf');
}

  .pageDiv {
    display: grid;
    grid-template-columns: auto auto auto;
    grid-row-gap: 30px;
    row-gap: 30px;
    margin-top: 60px;
    margin-bottom: 30px;
    
  }
  
  .notfound {
    margin-top: 100px;
    text-align: center;
    color: rgb(194, 194, 194);
  }


.scrollbar {
    position: fixed;
    color: #fff;
    overflow: hidden;
}

.headerContainer {
    position: fixed;
    width: 100%;
    height: 40px;
    top: 0px;
    padding: 5px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    -webkit-backdrop-filter: saturate(180%) blur(20px);
    backdrop-filter: saturate(180%) blur(20px);
    background-color: rgba(0, 0, 0, 1);
    box-shadow: 0 0 5px 10px rgba(0, 0, 0, 0.9);
    z-index: 9999;
}

.headertitle {
    position: fixed;
    color : #fff;
}

.headerContainer img {
    position: absolute;
}

.Navbarback {
    left: 10px;
}

.searchImg {
    right: 25px;
}

.headerInputDiv {
    position: absolute;
    bottom : 9px;
    border: none;
    border-radius: 5px;
    height: 30px;
    animation: input-slider 0.1s linear;
}

  .listMainBox {
    text-align: center;
  }
  
  .listMainBox img {
      border-radius: 2px;
      
  }
  
  .listMainBox p {    
    color: rgb(194, 194, 194);
    text-align: center;
    margin: 0;
  
  }
  
@keyframes text-scroll {
    from {
      transform: translateX(100%);
    }
    to {
      transform: translateX(-100%);
    }
}
  
@keyframes input-slider {
    from {
      transform: translateX(100%);
    }
    to {
      transform: translateX(0%);
    }
}  
.loadMore{
  background-color: #fff !important;
  border: 1px solid white !important;
  padding: 10px 20px !important;
  border-radius: 5px !important;
  margin: 0 auto !important;
  display: flex !important;
}
